@import url("https://fonts.googleapis.com/css?family=Roboto&subset=latin-ext");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

* :focus {
  outline: 0;
}

.clearfix::before .clearfix::after,
.sidebar::before .clearfix::after,
.sidebar a::before .clearfix::after,
.clearfix::before .sidebar::after,
.sidebar::before .sidebar::after,
.sidebar a::before .sidebar::after,
.clearfix::before .sidebar a::after,
.sidebar .clearfix::before a::after,
.sidebar::before .sidebar a::after,
.sidebar .sidebar::before a::after,
.sidebar a::before a::after {
  content: "";
  display: block;
  clear: both;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: inherit;
}

.icon {
  padding-left: 5px;
}

.icon:hover {
  fill: red;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url("../fonts/Mark Simonson - Proxima Nova Alt Regular-webfont.eot");
  src: url("../fonts/Mark Simonson - Proxima Nova Alt Regular-webfont.eot?#iefix") format("embedded-opentype"),url("../fonts/Mark Simonson - Proxima Nova Alt Regular-webfont.woff") format("woff"),url("../fonts/Mark Simonson - Proxima Nova Alt Regular-webfont.ttf") format("truetype"),url("../fonts/Mark Simonson - Proxima Nova Alt Regular-webfont.svg#proxima_nova_altregular") format("svg");
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #101022;
}

.text {
  font-size: 14px;
  color: white;
}

.text--small {
  font-size: 12px;
}

.text--large {
  font-size: 15px;
}

.text--colorDark {
  color: #080606;
}

.text--colorLight {
  color: #8a8995;
}

.btn {
  display: inline-block;
}

.btn--system {
  display: inline-block;
  border-radius: 50%;
  background-color: white;
  width: 10px;
  height: 10px;
  margin-left: 5px;
}

.btn--close {
  background-color: #ff594f;
}

.btn--minimize {
  background-color: #ffbc1b;
}

.btn--full {
  background-color: #00cd46;
}

body {
  background: url("../../assets/background.jpg") no-repeat;
  -o-object-fit: fill;
  object-fit: fill;
  width: 100vw;
  height: 100vh;
}

.system {
  display: block;
  width: 150px;
  background-color: #101022;
  border-radius: 10px 0 0 0;
  height: 40px;
  padding-left: 10px;
}

.system ul {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  line-height: 40px;
}

.system ul li {
  list-style-type: none;
}

.container {
  display: block;
  position: relative;
  width: 650px;
  height: 500px;
  margin: 0 auto;
  top: 50%;
  margin-top: -250px;
  border-radius: 10px;
  background-color: rgba(16,16,34,0.95);
  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.5);
}

.task-table {
  background-color: #f6f6f6;
  border-radius: 0 10px 10px 0;
  position: absolute;
  overflow-y: scroll;
  left: 150px;
  top: 0;
  right: 0;
  height: 100%;
  padding-top: 5px;
  padding-bottom: 5px;
}

.task-table__group-date {
  padding-left: 15px;
  margin-bottom: 6px;
}

.task-table__element {
  position: relative;
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  background-color: white;
  margin-left: 12px;
  margin: 0 15px 5px 15px;
  transition: background-color .2s ease;
}

.task-table__element a {
  position: absolute;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.task-table__element svg {
  position: relative;
  z-index: 1;
  stroke: #f6f6f6;
  float: right;
  top: 50%;
  margin-top: 10px;
  margin-right: 10px;
}

.task-table__element span {
  line-height: 38px;
  padding-left: 12px;
}

.task-table__element:hover {
  background-color: #ededed;
}

.sidebar {
  display: block;
  width: 150px;
  height: calc(100% - 40px);
  float: left;
  transition: background-color .2s ease;
}

.sidebar li {
  height: 53px;
  list-style-type: none;
}

.sidebar li.active {
  background-color: #292a48;
}

.sidebar li:hover {
  background-color: #202138;
}

.sidebar a {
  width: 100%;
  display: block;
  padding-left: 10px;
}

.sidebar img {
  display: inline-block;
  vertical-align: middle;
}

.sidebar span {
  vertical-align: middle;
  line-height: 53px;
  margin-left: 10px;
}

.sidebar span.counter {
  float: right;
  padding-right: 15px;
}


